import { paymentService } from "../../services/payment.service";
import { saveCurrentUser } from "../user/user-slice";

export const createContactsTransaction = async (dispatch, contacts) => {
    try {
        const updatedUser = await paymentService.createContactsPurchase(contacts)
        if (updatedUser) dispatch(saveCurrentUser(updatedUser))
    } catch (err) {
        throw err
    }
}

export const refundContactTransaction = async (dispatch, transactionId, userId) => {
    try {
        const updatedUser = await paymentService.removeContact(transactionId, userId)
        if (updatedUser) dispatch(saveCurrentUser(updatedUser))
    } catch (error) {
        throw error
    }
}