import { PaymentEP } from "../config/config"
import { contactTransType } from "../constants/contactTransType"
import { ContactTransaction } from "../model/contactTransaction/ContactTransaction"
import { creditTransactionService } from "./creditTransaction.service"
import { httpService } from "./http.service"
import { userService } from "./user.service"

export const paymentService = {
    query,
    create,
    createOne,
    createContactsPurchase,
    removeContact,
}

export async function query(filterBy = {}) {
    try {
        const res = await httpService.get(PaymentEP.default)
        if (res.status === 'ok') return res.content
        else return new Error('Cannot get territories')
    } catch (err) {
        throw err
    }
}

async function createOne(credit) {
    try {
        const transaction = await creditTransactionService.createCreditTransaction(credit)
        const res = await httpService.post(`${PaymentEP.default}/create`, [transaction])
        if (res.status === 'ok') return res.content
        else throw new Error("Couldn't purchase credit")
    } catch (err) {
        throw err
    }
}

async function create(credits) {
    try {
        const transactions = await Promise.all(
            credits.map(credit => creditTransactionService.createCreditTransaction(credit))
        )
        const res = await httpService.post(`${PaymentEP.default}/create`, transactions)
        if (res.status === 'ok') return res.content
        else throw new Error("Couldn't create territory")
    } catch (err) {
        throw err
    }
}

async function createContactsPurchase(contacts) {
    try {
        const userId = userService.getLoggedinUser()._id
        const transactions = _createContactTransactions(contacts, userId)
        const content = { transactions, userId, type: contactTransType.contactPurchase }
        const res = await httpService.post(PaymentEP.contact, content)
        if (res.status === 'ok') return res.content
        else throw new Error('Cannot purchase')
    } catch (err) {
        throw err
    }
}

async function removeContact(transactionId, userId) {
    try {
        const content = { userId, transactionId, type: contactTransType.contactRemove }
        const res = await httpService.post(PaymentEP.remove, content)
        if (res.status === 'ok') return res.content
        else throw new Error('Cannot refund')
    } catch (err) {
        throw err
    }
}

function _createContactTransactions(contacts, userId) {
    return contacts.map(contact => {
        return _createContactTransaction(contact, userId)
    })
}

function _createContactTransaction(contact, userId) {
    return new ContactTransaction(contact, userId)
}